<template>
    <div class='bookmark'>
         <div class="container">
      <div class="header clearfix">
        <nav>
        </nav>
        <h3 class="text-muted">Bookmarker</h3>
      </div>

      <div class="jumbotron">
        <h2>Bookmark Your Favorite Sites</h2>
          <div class="form-group">
            <label>Site Name</label>
            <input type="text" class="form-control" v-model="bookmark.name" placeholder="Website Name">
          </div>
          <div class="form-group">
            <label>Site URL</label>
            <input type="text" class="form-control" v-model="bookmark.url" placeholder="Website URL">
          </div>

          <button class="bth bth-primary" @click='addBook' type="submit">Submit</button>
      </div>

      <div class="row marketing">
        <div class="col-lg-12">
        <tbody>
        <tr v-for="(book,key) in bookmarks">
          <td><a v-bind:href="book.url">{{book.name}}</a></td>
          <td><span class="glyphicon glyphicon-trash" aria-hidden="true" v-on:click="removeBook(key)"></span></td>
        </tr>
      </tbody>
        </div>
      </div>
        <p>&copy; 2016 Bookmarker, Inc.</p>
      </footer>


    </div> 
    </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig';

var md5 = require('md5');
export default {
    name: 'Bookmark',
    data(){
        return{
            bookmark: {
                name:'',
                url:''
        },
        uid:'',
        bookmarks:{}
        }
    },
  methods:{
    addBook: function () {
        this.bookmark.url = "http://"+this.bookmark.url;
        //booksRef.push(this.bookmark);
        db.ref('users/'+ this.uid +'/bookmarks/').push(this.bookmark);
        this.bookmark.name = '';
      },
    removeBook: function (key) {
        db.ref('users/'+ this.uid +'/bookmarks/').child(key).remove()
      }
  },
  created() {
    var user = firebase.auth().currentUser;
 
    if (user){
        var uid = md5(user.email);
        this.uid = uid;
        db.ref('users/' + uid).on('value',
        (snapshot) => {
          if (snapshot) {
            console.log(snapshot.val());
            this.bookmarks = snapshot.val().bookmarks;
          }
      });
    }
    else{
        this.$router.push( { name: 'login'});
    }

  }
}

</script>

<style>

</style>


