<template>
    <div class='profile'>
        <h3>Profile</h3>
        <h2>User information:</h2>
        <div v-if="user.photoURL">
        <p>Profile Photo:</p>
        <img :src="user.photoURL" style="height: 120px">
        </div>
        <br>
        <p>ID: {{ user.userId }}</p>
        <p v-if="user.name">Name: {{ user.name }}</p>
        <p>Email: {{ user.email }}</p>
        <p>Website: {{ user.url }}</p>
        <p>Phone: {{ user.phone }}</p>
        
        <button @click="logout">Logout</button>
    </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'

var md5 = require('md5');

export default {
  name: 'profile',
  data () {
    return {
      user: {
        photoURL: '',
        userId: '',
        name: '',
        email: '',
        url: '',
        phone: ''
      }
    }
  },
  methods: {
    logout() {
      firebase.auth().signOut().then( () => {
        this.$router.push( { name: 'login'});
      })
    }
  },
  created() {
    var user = firebase.auth().currentUser;

    if (user) {
      this.user.photoURL = user.photoURL;
      this.user.userId = user.uid;
      this.user.name = user.displayName;
      this.user.email = user.email;

      var uid = md5(user.email);
      console.log(uid);

      db.ref('users/' + uid).once('value',
        (snapshot) => {
          if (snapshot) {
            console.log(snapshot);
            this.user.name = snapshot.val().name;
            this.user.url = snapshot.val().url;
            this.user.phone = snapshot.val().phone;
          }
      });
    }
    else{
        this.$router.push( { name: 'login'});
    }
  }

}
</script>

<style>

</style>


